import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import { joinPost, loginPost, logoutGet, forSession } from "../api/memberApi";
import { getCookie, removeCookie, setCookie } from "../util/cookieUtil";

const initState = {
  email: "",
};

//  
const loadMemberCookie = () => {
  const memberInfo = getCookie("member");
  console.log(
    "loginSlice.js loadMemberCookie()   member   -> ",
    memberInfo
  );
  const accessToken = getCookie("accessToken");

  //if (memberInfo && memberInfo.user) {         
  if (memberInfo && memberInfo.email) {
    // if (memberInfo && memberInfo.nickname) {
    //   memberInfo.nickname = decodeURIComponent(memberInfo.nickname);
    // }
    return memberInfo.email;
  }
  return { ...initState };
};

//   
export const forSessionAsync = createAsyncThunk("forSessionAsync", (param) => {
  return forSession(param);
});

// 
export const loginPostAsync = createAsyncThunk("loginPostAsync", (param) => {
  return loginPost(param);
});

// 
export const joinPostAsync = createAsyncThunk("joinPostAsync", (param) => {
  return joinPost(param);
});

// 
export const logoutGetAsync = createAsyncThunk("logoutGetAsync", () => {
  console.log(" logoutGetAsync() Thunk()");
  return logoutGet();
});

const loginSlice = createSlice({
  name: "loginSlice",
  initialState: loadMemberCookie() || initState,
  // initialState: initState,

  reducers: {
    // login: (state, action) => {
    //   const data = action.payload;
    //   console.log(
    //     "loginSlice login axios   payload  => ",
    //     data
    //   );
    //   //
    //   //setCookie("member", JSON.stringify(data), 1);
    //   //return data;
    //   return { email: data };
    // },
    logout: (state, action) => {
      removeCookie("member");
      removeCookie("accessToken");
      console.log(
        "loginSlice logout  axios   payload  => ",
        action.payload
      );
      return { ...initState };
    },
    // join: (state, action) => {
    //   const data = action.payload;
    //   return data;
    // },
  },
  //
  extraReducers: (builder) => {
    builder
      .addCase(loginPostAsync.pending, (state, action) => {})
      .addCase(loginPostAsync.fulfilled, (state, action) => {
        const payload = action.payload;
        console.log(
          "loginSlice loginPostAsync  fulfilled  payload  => ",
          payload
        );

        const jsonstringify = payload.user;
        console.log(
          "loginSlice loginPostAsync  fulfilled  JSON payload.user => ",
          jsonstringify
        );

        //
        if (!payload.error) {
          console.log(
            "loginSlice loginPostAsync  fulfilled    member  "
          );
          setCookie("member", jsonstringify, 1);
        }
        return payload.user.email; //
      })
      .addCase(loginPostAsync.rejected, (state, action) => {
        console.log(
          "loginSlice loginPostAsync  rejected  payload  => ",
          action.payload
        );
      })
      .addCase(joinPostAsync.fulfilled, (state, action) => {
        const payload = action.payload;

        console.log(
          "loginSlice joinPostAsync  fulfilled  payload  => ",
          payload
        );
        //return payload;
      })
      .addCase(joinPostAsync.pending, (state, action) => {
        console.log(
          "loginSlice joinPostAsync  pending  payload  => ",
          action
        );
      })
      .addCase(joinPostAsync.rejected, (state, action) => {
        const payload = action.payload;
        console.log(
          "loginSlice joinPostAsync  rejected  payload  => ",
          payload
        );
      })

      .addCase(logoutGetAsync.pending, (state, action) => {
        console.log(
          "loginSlice logoutGetAsync  pending  payload  => ",
          action
        );
      })
      .addCase(logoutGetAsync.fulfilled, (state, action) => {
        const payload = action.payload;

        console.log(
          "loginSlice logoutGetAsync  fulfilled  payload  => ",
          payload
        );
        removeCookie("member");
        removeCookie("accessToken");

        return { ...initState };
      })
      .addCase(logoutGetAsync.rejected, (state, action) => {
        const payload = action.payload;
        console.log(
          "loginSlice logoutGetAsync  rejected  payload  => ",
          payload
        );
      })
      .addCase(forSessionAsync.pending, (state, action) => {})
      .addCase(forSessionAsync.fulfilled, (state, action) => {
        const payload = action.payload;
        console.log(
          "loginSlice forSessionAsync  fulfilled  payload  => ",
          payload
        );
        return payload;
      })
      .addCase(forSessionAsync.rejected, (state, action) => {
        const payload = action.payload;
        console.log(
          "loginSlice forSessionAsync  rejected  payload  => ",
          payload
        );
      });
  },
});

export const { logout } = loginSlice.actions;

export default loginSlice.reducer;
