const jwtAxios = axios.create();

//jwt 
const refreshJWT = async (refreshToken, accessToken) => {
  const host = API_SERVER_HOST;

  console.log("jwtUtil.js refreshJWT ");

  const header = { headers: { Authorization: `Bearer ${accessToken}` } };

  try {
    const res = await axios.get(
      `${host}/api/member/refreshToken?refreshToken=${refreshToken}`,
      header
    );
    console.log("jwtUtil.js refreshJWT axios  -> ", res);
    return res.data;
  } catch (err) {
    console.log("jwtUtil.js refreshJWT catch() -> ", err);
    //    if (err.response.data.name === "TokenExpiredError") {
    if (err.response.data.error === "ERROR_REFRESH_TOKEN") {
      return Promise.reject({ response: { data: { error: "REQUIRE_LOGIN" } } });
    }
  }
};

