const responseFail = async (err) => {
  console.log("jwtUtil.js responseFail()  err->  ", err);

  if (
    err.status === 419 ||
    err.code === "ERR_BAD_REQUEST" ||
    err.response.data.error === "ERROR_ACCESS_TOKEN" ||
    err.response.data.error === "EXPIRED_ACCESS_TOKEN"
  ) {
    console.log("jwtUtil.js responseFail()   ");

    const memberCookieValue = getCookie("member");
    console.log(
      "jwtUtil.js responseFail()  member-> ",
      memberCookieValue
    );

    if (memberCookieValue && memberCookieValue.user.refreshtoken) {
      const refreshtoken = memberCookieValue.user.refreshtoken;
      //member
      console.log(
        "jwtUtil.js responseFail()  member refreshtoken -> ",
        refreshtoken
      );
      const accessToken = getCookie("accessToken");

      const result = await refreshJWT(refreshtoken, accessToken); //

      console.log("jwtUtil.js responseFail()  refreshJWT() ->  ", result);

      setCookie("member", JSON.stringify(result), 1);

      //
      const originalRequest = err.config;
      originalRequest.headers.Authorization = `Bearer ${result.accessToken}`;
      return await axios(originalRequest);
    } else {
      //member
      console.log("jwtUtil.js responseFail()  err ->  ", err);

      return Promise.reject(err);
    }
  }

  if (err.response.data.error === "ERROR_REFRESH_TOKEN") {
    return Promise.reject(err);
  }
  if (err.message === "Network Error" || err.name === "AxiosError") {
    return Promise.reject(err);
  }
};

jwtAxios.interceptors.request.use(beforeReq, requestFail);
jwtAxios.interceptors.response.use(beforeRes, responseFail);

export default jwtAxios;
