import { useDispatch, useSelector } from "react-redux";
import { createSearchParams, Navigate, useNavigate } from "react-router-dom";
import {
  joinPostAsync,
  loginPostAsync,
  logout,
  logoutGetAsync,
  forSessionAsync,
} from "../slices/loginSlice";
import { removeCookie } from "../util/cookieUtil";

const useCustomLogin = () => {
  const navigate = useNavigate();

  const dispatch = useDispatch();

  const loginState = useSelector((state) => state.loginSlice);

  //  console.log("loginState -> ", loginState);
  const isLogin = loginState.email !== "" ? true : false;

  const forSession = async () => {
    const result = await dispatch(forSessionAsync()); // return
    return result;
  };

  const doLogin = async (loginParam) => {
    console.log("useCustomLogin doLogin() ");
    const action = await dispatch(loginPostAsync(loginParam));
    console.log(
      "useCustomLogin doLogin()  axios    => ",
      action.payload
    );
    return action.payload;
  };

  const doJoin = async (joinParam) => {
    console.log("useCustomLogin doJoin() ");

    const action = await dispatch(joinPostAsync(joinParam));
    console.log(
      "useCustomLogin doJoin()  axios    => ",
      action.payload
    );
    return action.payload;
  };

  const doLogout = async () => {
    console.log("useCustomLogin doLogout() ");
    dispatch(logout());

    // const action = await dispatch(logoutGetAsync());
    // console.log(
    //   "useCustomLogin doLogout()  axios    => ",
    //   action.payload
    // );
    // return action.payload;
  };

  const moveToPath = (path) => {
    navigate({ pathname: path }, { replace: true });
  };

  const moveToLogin = () => {
    navigate({ pathname: "/member/login" }, { replace: true });
  };

  const moveToJoin = () => {
    navigate({ pathname: "/member/join" }, { replace: true });
  };

  const moveToLoginReturn = () => {
    return <Navigate replace to="/member/login" />;
  };

  //useEffect   
  const effectException = (ex) => {
    console.log("useCustomLogin.js effectException   ", ex);
  };

  //
  const exceptionHandle = (ex) => {
    console.log("exceptionHandle() ", ex);

    if (!isLogin) {
      removeCookie("member");
      removeCookie("accessToken");

      return navigate({ pathname: "/login" }, { replace: true });
    }

    if (ex.response && ex.response.data) {
      const errorMsg = ex.response.data.error;

      const errorStr = createSearchParams({ error: errorMsg }).toString();

      // if (
      //   errorMsg === "REQUIRE_LOGIN" ||
      //   errorMsg === "ERROR_ACCESS_TOKEN" ||
      //   ex.response.data.code === 401
      // ) {
      //   alert(" .");
      //   removeCookie("member");
      //   removeCookie("accessToken");
      //   navigate({ pathname: "/login", search: errorStr });
      //   return;
      // }

      // if (ex.response.data.error === "ERROR_ACCESSDENIED") {
      //   alert(" .");
      //   removeCookie("member");
      //   removeCookie("accessToken");

      //   navigate({ pathname: "/login", search: errorStr });
      //   return;
      // }
    }

    //
    if (
      ex.name === "ERR_BAD_RESPONSE" ||
      ex.code === "AxiosError" ||
      ex.code === "ERR_BAD_RESPONSE" ||
      ex.message === "Network Error"
    ) {
      navigate({ pathname: "/" }, { replace: true });
      return;
    }

    if (isLogin && ex !== null) {
      navigate({ pathname: "/" }, { replace: true });
      return;
    }
  };

  return {
    loginState,
    isLogin,
    doLogin,
    doLogout,
    moveToPath,
    moveToLogin,
    moveToLoginReturn,
    moveToJoin,
    doJoin,
    exceptionHandle,
    //useEffect jwt
    effectException,
    //
    forSession,
  };
};

export default useCustomLogin;
